.cards-horizontal {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
}

.cards_item-horizontal {
  display: flex;
  padding: 1rem;
  max-width: 400px;
  margin: 0 auto;
}
@media (min-width: 600px) and (max-width: 899px) {
  .cards_item-horizontal {
    max-width: 325px;
  }
}
@media (min-width: 1200px) and (max-width: 1799px) {
  .cards_item-horizontal {
    max-width: 360px;
  }
}
@media (min-width: 1800px) {
  .cards_item-horizontal {
    max-width: 400px;
  }
}

.card_image-horizontal {
  max-height: 250px;
  margin-top: -24px;
}
.card_image-horizontal img {
  width: 100%;
  max-width: 800px;
  height: 100%;
  object-fit: cover;
}
.card_image-horizontal:after {
  content: "";
  display: block;
  width: 100%;
  height: 100px;
  position: relative;
  top: -100px;
  background: linear-gradient(0deg, rgb(255, 217, 61) 10%, rgba(0, 0, 0, 0) 100%);
}

.card-horizontal {
  background-color: rgb(255, 217, 61);
  border-radius: 0.25rem;
  box-shadow: 0 20px 40px -14px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.card_content-horizontal {
  padding: 1rem;
  z-index: 1;
  display: block;
}

.card_title-horizontal {
  margin-top: -50px;
  margin-bottom: 24px;
  text-shadow: rgb(255, 217, 61) 1px 0 15px;
  color: rgb(0, 0, 0);
}

.card_text-horizontal {
  color: rgb(0, 0, 0);
  font-family: "Raleway", sans-serif;
  text-shadow: rgb(0, 0, 0) 1px 0 30px;
  color: rgb(0, 0, 0);
  font-weight: 200;
}
.card_text-horizontal p {
  max-width: 700px;
}

.card_price-horizontal {
  position: relative;
  top: 10px;
  right: 16px;
  text-align: right;
  color: rgb(246, 241, 233);
  font-family: "Raleway", sans-serif;
  height: 24px;
  font-size: 36px;
  font-weight: 400;
  text-shadow: black 3px 0px 6px;
}

#item_fig .card:after {
  content: "Seasonal";
  font-family: "Raleway", sans-serif;
  color: rgba(234, 225, 175, 0.8);
  font-weight: 600;
  text-transform: uppercase;
  display: block;
  width: 10ch;
  height: 1.5em;
  padding: 0.2em 0.4em;
  position: relative;
  bottom: calc(100% - 2.5em);
  left: 1em;
}

.icon {
  position: relative;
  /* Adjust these values accordingly */
  top: 5px;
  left: 5px;
}

.social {
  display: flex;
}

.social li {
  list-style: none;
}

.social li a {
  display: block;
  position: relative;
  width: 40px;
  height: 40px;
  line-height: 47px;
  font-size: 20px;
  text-align: center;
  text-decoration: none;
  color: #404040;
  margin: 0 30px;
  transition: 0.5s;
}

.social li a span {
  position: absolute;
  transition: transform 0.5s;
}

.social li a span:nth-child(1),
.social li a span:nth-child(3) {
  width: 100%;
  height: 1px;
  background: #404040;
}

.social li a span:nth-child(1) {
  top: 0;
  left: 0;
  transform-origin: right;
}

.social li a:hover span:nth-child(1) {
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.5s;
}

.social li a span:nth-child(3) {
  bottom: 0;
  left: 0;
  transform-origin: left;
}

.social li a:hover span:nth-child(3) {
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.5s;
}

.social li a span:nth-child(2),
.social li a span:nth-child(4) {
  width: 3px;
  height: 100%;
  background: #404040;
}

.social li a span:nth-child(2) {
  top: 0;
  left: 0;
  transform: scale(0);
  transform-origin: bottom;
}

.social li a:hover span:nth-child(2) {
  transform: scale(1);
  transform-origin: top;
  transition: transform 0.5s;
}

.social li a span:nth-child(4) {
  top: 0;
  right: 0;
  transform: scale(0);
  transform-origin: top;
}

.social li a:hover span:nth-child(4) {
  transform: scale(1);
  transform-origin: bottom;
  transition: transform 0.5s;
}

.facebook:hover {
  color: #3b5998;
}

.facebook:hover span {
  background: #3b5998;
}

.twitter:hover {
  color: #1da1f2;
}

.twitter:hover span {
  background: #1da1f2;
}

.instagram:hover {
  color: #c32aa3;
}

.instagram:hover span {
  background: #c32aa3;
}

.google:hover {
  color: #dd4b39;
}

.google:hover span {
  background: #dd4b39;
}

.social li a .twitter {
  color: #1da1f2;
}

.social li a:hover:nth-child(3) {
  color: #c32aa3;
}

.social li a:hover:nth-child(4) {
  color: #dd4b39;
}

